import React, { Component } from 'react';
import Col from 'react-bootstrap/Col';
import axios from 'axios';
class ParentReviewSec extends Component {
    constructor(props) {
        super(props);
            this.state = { 
                    invalidUrl:'' ,
                    ReviewNoteOverviewVal:""       
            };
        }
        componentDidMount() {
        // sessionStorage.setItem('user_email',"deepika.r@oracle.com");
// alert("test");
        var url = window.location.href;
        //   alert('test');
        var statusGov=url.split('&Governance=');
     // alert(statusGov[1]);
        if(statusGov[1]!='Y'){
            this.setState({
                // class:'col-md-6 pull-right',
                reviewClass:' rightNav rightSection'
            });
        }
        var MyAssetStat=url.split('&MyASSET=');
        if(MyAssetStat[1]!='Y'){
            
            this.setState({
                reviewClass:' rightNav rightSection'
                
            });
        }
        var url = window.location.href;
        var ID=url.split('?');
        global.IDAsset=ID[1];
        var resparam=  {
              assetId:global.IDAsset,
              userId:sessionStorage.getItem('user_email'),
              "user_email":sessionStorage.getItem("user_email")
          }
        axios.post(global.Ip + global.Port + '/asset/socialData/',resparam,{
            headers:resparam
        }) 
        .then(res => {
          var likedBy="";
          var IdforLikeunlike=null;
          var likename;
            for(var i=0;i<=res.data.LIKES.length-1;i++){
                if(res.data.LIKES[i].LIKE_USERNAME==null){
                     likename='-';
                     var firsttwoletterLike = '-';
                }else{
                     likename=res.data.LIKES[i].LIKE_USERNAME;
                     var likeNamesplit=likename.split(" ");
// console.log(likeNamesplit);
                     var firstletterlike = likeNamesplit[0].substring(0,1);
                     if(likeNamesplit[1]!=undefined && likeNamesplit[1]!=null && likeNamesplit[1]!=""){
                        var secondletterlike= likeNamesplit[1].substring(0,1);

                     }else{
                        var secondletterlike= "";

                     }
                     var firsttwoletterLike=firstletterlike + secondletterlike;
                    // var firsttwoletterLike = likename.substring(0,1);
                }           
               likedBy += '<div class="loginname likepane" title='+likename+'><label>'+firsttwoletterLike+'</label></div>' ;
               if(res.data.USER_LIKE.length==1){
                     IdforLikeunlike=res.data.USER_LIKE[0].LIKE_ID;
                    var likeImg='../img/like_orange.png';
                }else{
                    var likeImg='../img/like2.png';             
               }
               this.setState({
                likeImg:likeImg
                
                 });
            }



          






            var CommentBy="";
            for(var j=0;j<=res.data.COMMENTS.length-1;j++){
                if(res.data.COMMENTS[j].COMMENT_USERNAME!=null && res.data.COMMENTS[j].COMMENT_USERNAME!="" && res.data.COMMENTS[j].COMMENT_USERNAME!=undefined)
                {
                var commentname=(res.data.COMMENTS[j].COMMENT_USERNAME).toUpperCase();
                }else{
                    var commentname="**";
                }
                var desc=res.data.COMMENTS[j].COMMENT_COMMENT;
                var commentNamesplit=commentname.split(" ");

                var firstletter = commentNamesplit[0].substring(0,1);
                if(commentNamesplit[1]!=undefined && commentNamesplit[1]!=null && commentNamesplit[1]!=""){
                    var secondletter= commentNamesplit[1].substring(0,1);

                 }else{
                    var secondletter= "";

                 }



                // var secondletter= commentNamesplit[1].substring(0,1);
                var firsttwoletter=firstletter + secondletter;
                CommentBy += ' <div class="media"><span class="loginname commentBox" title='+commentname+'><label>'+firsttwoletter+'</label></span><div class="media-body"><h6 class="mb-0">'+commentname+' </h6><p>'+desc+'</p> </div></div>' ;
              }
            if(res.data.LIKES.length<1){
                var LikeCount='Be first to like Asset';
            }else{
                var LikeCount= res.data.LIKES.length;
            }
            if(res.data.sharecount<1){
                var shareCount='Be first to share Asset';
            }else{
                var shareCount= res.data.sharecount;
            }
            if(res.data.COMMENTS.length<1){
                var Commentcnt='Be first to Comment on Asset';
            }else{
                var Commentcnt= res.data.COMMENTS.length;
            }
            this.setState({
                socialData:res.data,
                likesCount:LikeCount,
                shareCount:shareCount,
                likedBy:likedBy,
                likeId:IdforLikeunlike,
                CommentCount:Commentcnt,
                CommentBy:CommentBy
                //likeImg:likeImg
            })
        });
         }
        handleChange = (e) => {
            this.setState({
                [e.target.name]: e.target.value
            })
         }

      ApproveAsset = (val) => {
       //selected checkboxes value
        var markedCheckbox = document.querySelectorAll('input[name="questionnaire"]:checked');
        var selectedQues=[];
        for (var checkbox of markedCheckbox) {
            selectedQues.push(checkbox.value);
        //alert(checkbox.value + ' ');
        }
        var objJson = [];
            if(this.state.ReviewStatus==""){
                alert('Please select status');
                return false;
            }
            else if(this.state.ReviewNoteOverviewVal==""){
                alert('Please enter Message');
                return false;
            }else{
                var txt;
                var r = window.confirm("Are you sure you want to change the asset status to "+this.state.ReviewStatus);
                if (r == true) {
                    var data = {
                        "note": this.state.ReviewNoteOverviewVal,
                        "questions":selectedQues
                    }
                    objJson.push(data);
        
                    var reqParm = {
                        "review_note": objJson, "assetId": global.ID,
                        "asset_status": this.state.ReviewStatus,
                        "user_email": sessionStorage.getItem("user_email")
        
        
                    };
                    axios.post(global.Ip + global.Port + '/governance/postreviewnote', reqParm, {
                        headers: {
                            "user_email": sessionStorage.getItem("user_email")
        
                        }
                    }).then(res => {
                        //console.log(res.data);
                        alert(res.data.status);
                        this.setState({
                            RedirectBack: 'true'
                        });
                    });
                } else {
                txt = "You pressed Cancel!";
                }



               
            }
            //var SelectedTabForSection='Approve';
            

        } 
  //  }

      
      
        
    render() {
      // alert(this.state.likeImg);
    return (
                        

<Col md={4} className={this.state.reviewClass}>
<div className="form-group col reviewSec" ><label class="text-center form-label">Review Box</label> 
    <label for="cars"></label><br/>
    <label for="cars">Choose a status:</label>
    <select name="ReviewStatus" id="ReviewStatus" onChange={e => this.handleChange(e)}>
        <option value="Live">Approve</option>
        <option value="Pending Rectification">Send for Rectification</option>
        <option value="Reject">Reject</option>
    </select><br/>
    <label class="">Message</label>
    <textarea aria-label="With textarea" onChange={e => this.handleChange(e)} name="ReviewNoteOverviewVal" id="ReviewNoteOverviewVal" class="form-control">    </textarea>
    <div id="checkboxes">
    <label class="">Questionnaire</label>
        <ul>
            <li><input type="checkbox" name="questionnaire" value="q1"/> checkbox 1</li>
            <li><input type="checkbox" name="questionnaire" value="q2"/> checkbox 2</li>
            <li><input type="checkbox" name="questionnaire" value="q3"/> checkbox 3</li>
            <li><input type="checkbox" name="questionnaire" value="q4"/> checkbox 4</li>
            <li><input type="checkbox" name="questionnaire" value="q5"/> checkbox 5</li>

        </ul>
        <button type="button" class="btn mt-20  btn-primary" onClick={e => this.ApproveAsset(e)}>Submit</button>
    </div>
</div>
</Col>


    );
    }
  }
  
  export default ParentReviewSec;